<template>
  <div :class="['visualizer']">
    <template
      v-for="(note, index) of plotNotes"
    >
      <Note
        v-bind="{
          instance,
          note
        }"
        relativePositioning
        :key="`note_${index}`"
      />
      <span :class="['distance', `distance--${distanceBetween(note, plotNotes[index + 1]) * 2}`]" :key="`distance_${index}`" v-if="plotNotes.length !== index + 1"></span>
    </template>
  </div>
</template>

<script>
import { $bus } from '../main';
import Note from './Note';

import { distanceBetween } from '../entities/intervals';

export default {
  name: 'board',
  props: {
    plot: {
      type: [ Array, String ],
      default: 'c d e f g a b c'
    }
  },
  computed: {
    instance() {
      return this._uid;
    },
    plotNotes() {
      return this.plot.split(' ');
    }
  },
  methods: {
    distanceBetween(start, end) {
      if (!start || !end) {
        return false;
      }
      console.log(start, end);
      return distanceBetween(start, end);
    }
  },
  components: {
    Note
  },
  created() {
    $bus.$on('click-note', ({ instance, note }) => {
      if (instance !== this.instance) {
        return;
      }

      this.$emit('click-note', note);
    });
  }
}
</script>

