<script>
import { $bus } from '../main';
// import Note from './Note';

import { getChromaticScale, distanceBetween } from '../entities/intervals';

export default {
  name: 'piano',
  data: () => ({
    scale: null
  }),
  props: {
    plot: {
      type: [ Array, String ],
      default: 'c d e f g a b c'
    }
  },
  computed: {
    instance() {
      return this._uid;
    },
    plotNotes() {
      return this.plot.split(' ');
    }
  },
  methods: {
    distanceBetween(start, end) {
      if (!start || !end) {
        return false;
      }
      console.log(start, end);
      return distanceBetween(start, end);
    }
  },
  // components: {
  //   Note
  // },
  created() {
    this.scale = getChromaticScale('c');

    $bus.$on('click-note', ({ instance, note }) => {
      if (instance !== this.instance) {
        return;
      }

      this.$emit('click-note', note);
    });
  }
}
